import Vue from 'vue'
import Router from 'vue-router'
import store from './store'

Vue.use(Router)

const router = new Router({
  mode: 'hash',

  // scrollBehavior(to,from,savePosition){
  //   if(savePosition){
  //    console.log(savePosition,111)
  //     return savePosition
  //   }else{
  //     return {x:0,y:0}
  //   }
  // },

  base: process.env.BASE_URL,
  routes: [
    {
      path: '/pages',
      component: () => import('@/views/pages/Index'),
      children: [
        {
          name: 'Lock',
          path: 'lock',
          component: () => import('@/views/pages/Lock'),
        },
        {
          name: 'Login',
          path: 'login',
          component: () => import('@/views/pages/Login'),
        },
        {
          name: 'Pricing',
          path: 'pricing',
          component: () => import('@/views/pages/Pricing'),
        },
        {
          name: 'Register',
          path: 'register',
          component: () => import('@/views/pages/Register'),
        },
      ],
    },
    {
      path: '/login',
      meta: { allowUnAuthoried: true },
      component: () => import('@/views/Login')
    },
    {
      path: '/dingLogin',
      meta: { allowUnAuthoried: true },
      component: () => import('@/views/dingLogin')
    },
    {
      path: '/dingLogin2',
      meta: { allowUnAuthoried: true },
      component: () => import('@/views/dingLogin2')
    },
    {
      name: 'ContractDetails',
      path: '/contractDetails',
      component: () => import('@/views/dashboard/contract/ContractDetails'),
    },
    {
      path: '/',
      component: () => import('@/views/dashboard/Index'),
      children: [
        // Dashboard
        {
          name: 'Dashboard',
          // meta: { allowUnAuthoried: true },
          path: '',
          component: () => import('@/views/dashboard/Dashboard'),
        },
        // Pages
        {
          name: 'RTL',
          path: 'pages/rtl',
          component: () => import('@/views/dashboard/pages/Rtl'),
        },
        {
          name: 'User Profile',
          path: 'pages/user',
          component: () => import('@/views/dashboard/pages/UserProfile'),
        },
        {
          name: 'Timeline',
          path: 'pages/timeline',
          component: () => import('@/views/dashboard/pages/Timeline'),
        },
        // Components
        {
          name: 'Buttons',
          path: 'components/buttons',
          component: () => import('@/views/dashboard/component/Buttons'),
        },
        {
          name: 'Grid System',
          path: 'components/grid-system',
          component: () => import('@/views/dashboard/component/Grid'),
        },
        {
          name: 'Tabs',
          path: 'components/tabs',
          component: () => import('@/views/dashboard/component/Tabs'),
        },
        {
          name: 'Notifications',
          path: 'components/notifications',
          component: () => import('@/views/dashboard/component/Notifications'),
        },
        {
          name: 'Icons',
          path: 'components/icons',
          component: () => import('@/views/dashboard/component/Icons'),
        },
        {
          name: 'Typography',
          path: 'components/typography',
          component: () => import('@/views/dashboard/component/Typography'),
        },
        // Forms
        {
          name: 'Regular Forms',
          path: 'forms/regular',
          component: () => import('@/views/dashboard/forms/RegularForms'),
        },
        {
          name: 'Extended Forms',
          path: 'forms/extended',
          component: () => import('@/views/dashboard/forms/ExtendedForms'),
        },
        {
          name: 'Validation Forms',
          path: 'forms/validation',
          component: () => import('@/views/dashboard/forms/ValidationForms'),
        },
        {
          name: 'Wizard',
          path: 'forms/wizard',
          component: () => import('@/views/dashboard/forms/Wizard'),
        },
        // Tables
        {
          name: 'Regular Tables',
          path: 'tables/regular-tables',
          component: () => import('@/views/dashboard/tables/RegularTables'),
        },
        {
          name: 'Extended Tables',
          path: 'tables/extended-tables',
          component: () => import('@/views/dashboard/tables/ExtendedTables'),
        },
        {
          name: 'Data Tabels',
          path: 'tables/data-tables',
          component: () => import('@/views/dashboard/tables/DataTables'),
        },
        // Maps
        {
          name: 'Google Maps',
          path: 'maps/google-maps',
          component: () => import('@/views/dashboard/maps/GoogleMaps'),
        },
        {
          name: 'Full Screen Map',
          path: 'maps/full-screen-map',
          component: () => import('@/views/dashboard/maps/FullScreenMap'),
        },
        // Root level
        {
          name: 'Widgets',
          path: 'widgets',
          component: () => import('@/views/dashboard/Widgets'),
        },
        {
          name: 'Charts',
          path: 'charts',
          component: () => import('@/views/dashboard/Charts'),
        },
        {
          name: 'Calendar',
          path: 'calendar',
          component: () => import('@/views/dashboard/Calendar'),
        },
        {
          name: 'Organization',
          path: 'organization',
          component: () => import('@/views/dashboard/Organization'),
        },
        {
          name: 'Organization2',
          path: 'organization2',
          component: () => import('@/views/dashboard/Organization2'),
        },
        {
          name: '组织架构',
          path: 'organization3',
          component: () => import('@/views/dashboard/Organization3'),
        },
        {
          name: 'Organization4',
          path: 'organization4',
          component: () => import('@/views/dashboard/Organization4'),
        },
        {
          name: '角色管理',
          path: 'roles',
          component: () => import('@/views/dashboard/Roles'),
        },
        {
          name: '权限管理',
          path: 'permissions',
          component: () => import('@/views/dashboard/permission/Permissions'),
        },
        {
          name: 'CompanyManage',
          path: 'companyManage',
          component: () => import('@/views/dashboard/CompanyManage'),
          meta: { keepAlive: true },
        },
        {
          name: 'DictionaryManager',
          path: 'dictionaryManager',
          component: () => import('@/views/dashboard/DictionaryManager'),
        },
        {
          name: 'AddCompanyInfo',
          path: 'addCompanyInfo',
          component: () => import('@/views/dashboard/component/AddCompanyInfo'),
        },
        {
          name: 'EditCompanyInfo',
          path: 'editCompanyInfo',
          component: () => import('@/views/dashboard/component/EditCompanyInfo'),
        },
        {
          name: 'RenewalManage',
          path: 'renewalManage',
          component: () => import('@/views/dashboard/RenewalManage'),
        },
        {
          name: 'AddRenewalInfo',
          path: 'addRenewalInfo',
          component: () => import('@/views/dashboard/component/AddRenewalInfo'),
        },
        {
          name: 'EditRenewalInfo',
          path: 'editRenewalInfo',
          component: () => import('@/views/dashboard/component/EditRenewalInfo'),
        },
        {
          name: 'Contract',
          path: 'contract',
          component: () => import('@/views/dashboard/contract/Contract'),
        }
      ],
    },
    {
      path: '*',
      component: () => import('@/views/pages/Index'),
      children: [
        {
          name: '404 Error',
          path: '',
          component: () => import('@/views/pages/Error'),
        },
      ],
    },
  ],
})

router.beforeEach((to, from, next) => {
  if (!to.meta.allowUnAuthoried) { // 判断该路由是否需要登录权限
    if (store.getters.getToken) {
      next();
    } else {
      next({
        path: '/login'
      })
    }
  } else {
    next();
  }
});

export default router
