import axios from 'axios'
import qs from 'qs'
import store from '../store'
import router from '../router'
import auth from "@/utils/auth";

// post方式请求头
axios.defaults.headers.post['Content-Type'] = 'application/json';
// 请求超时时间
axios.defaults.timeout = 10000;

// 拦截器配置
axios.interceptors.request.use(
    config => {
        config.withCredentials = true
        const token = store.state.token;
        if (token) {
            config.headers.access_token = token;
        } else {
            // router.replace({path: '/login'})
        }

        // 增加belong_database
        const belong_database = auth.getDatabaseName();
        if (!!belong_database) {
            config.headers.belong_database = belong_database;
        }

        return config;
    }
)

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    if (!!response && !!response.data && "u100000" == response.data.errorCode) {
        alert(response.data.msg);
        router.replace({path: '/login'});
    }

    return response;
}, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
});

export const get = (url, data) => {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: data
        }).then(res => {
            resolve(res.data)
        }).catch(err => {
            reject(err, data)
        })
    })
}
export const post = (url, data) => {
    return new Promise((resolve, reject) => {
        axios.post(url, qs.parse(data)).then(res => {
            resolve(res.data)
        }).catch(err => {
            reject(err.data)
        })
    })
}
export const uploadPost = (url, data) => {
    return new Promise((resolve, reject) => {
        axios.post(url, data, {
            headers: {
                'Content-Type': 'multipart/form-data',
            }
        }).then(res => {
            resolve(res.data)
        }).catch(err => {
            reject(err.data)
        })
    })
}

